.page__background {
  z-index: -1;
  top: 0 !important;
  background-image: url(/src/image/back.png);
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
.page__content {
  position: static;
}
.page--home,
.page--group-select {
  display: flex;
  flex-flow: column;
}
.toolbar {
  position: static !important;
  z-index: 300;
}
.p-24 {
  padding: 24px;
  min-height: 24px;
}
.toolbar__title,
.toolbar__right,
.toolbar__left {
  width: 0;
}
.toolbar {
  flex-direction: column;
  margin-bottom: 8px;
  min-height: 80px;
  background-color: rgba(255, 255, 255, 0);
  background-image: none;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  box-shadow: 0 4px 16px rgba(36, 37, 41, 0.1);
  color: #232323;
  white-space: inherit;
}
@media (min-width: 876px) {
  .toolbar {
    flex-flow: nowrap;
    align-items: center;
    padding: 20px 120px;
    margin-bottom: 0;
    min-height: 88px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    box-shadow: 0 4px 10px 0 rgba(29, 30, 32, 0.1);
  }
}
.toolbar-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0);
}
.toolbar-container--without-margin {
  border-radius: 0;
  margin-bottom: 0;
  box-shadow: none;
}
.toolbar-container__calendar {
  position: absolute;
  top: 18px;
  right: 15px;
}
.toolbar-container__calendar .ant-picker {
  border: none;
  background-color: rgba(255, 255, 0, 0);
}
.toolbar-container__calendar .ant-picker-input input {
  display: none;
}
.toolbar-container__calendar .ant-picker-input svg {
  width: 30px;
  height: 30px;
}
.toolbar-container__title {
  width: 70%;
}
.toolbar-container__title ons-button {
  width: 100%;
  flex-direction: column !important;
  background-color: rgba(255, 255, 255, 0) !important;
  color: #ffffff !important;
  margin-top: 8%;
}
.toolbar-container__title_week {
  font-size: 14px;
  font-weight: normal;
  letter-spacing: 0pt;
}
.page-home {
  background-color: rgba(255, 255, 255, 0.5);
  padding: 8px;
}
.page-home__title {
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 36px;
}
.page-home__lessons_item {
  box-sizing: border-box;
  padding: 8px;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 12px;
}
.page-home__lessons_item .lesson-time {
  margin-left: 12px;
  margin-right: 24px;
  font-weight: bold;
}
.page-home__lessons_item .lesson-name .name {
  width: 100%;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
}
.page-home__lessons_item .lesson-name .audit {
  color: #9497AA;
}
.page-group-select {
  position: relative;
}
.page-group-select__logo {
  padding: 20px;
  width: 360px;
}
.page-group-select__content {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
}
.page-group-select__loader {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;
}
.page-group-select__title {
  margin-bottom: 24px;
  color: #dfdfdf;
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
}
.page-group-select__select-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
}
.page-group-select__select-container_select {
  width: 300px;
}
.page-group-select__btn-show {
  width: 100%;
}
.page-group-select__upload-container {
  position: absolute;
  bottom: 50px;
  left: 0;
  right: 0;
}
.page-group-select__upload-container_btn {
  max-width: 90%;
  margin: 0 auto;
}
ons-button[modifier~='button-main-node'] {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  flex: 1;
  padding: 0;
  height: 48px;
  color: #fff;
  border-radius: 12px;
  border: none;
  font-size: 16px;
  line-height: 24px;
  font-weight: bold;
  cursor: pointer;
  background-color: #26CA42;
  transition: opacity 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
ons-button[modifier~='button-main-node']:first-child {
  display: flex;
  align-items: center;
  justify-content: center;
}
ons-button[modifier~='button-main-node']:active {
  transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
  background-color: #26CA42;
}
ons-button[modifier~='button-main-node__groupSelect'] {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  flex: 1;
  padding: 0;
  height: 48px;
  color: #fff;
  border-radius: 12px;
  border: none;
  font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  font-size: 32px;
  line-height: 24px;
  font-weight: bold;
  cursor: pointer;
  letter-spacing: 2px;
  background-color: #26CA42;
  transition: opacity 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
ons-button[modifier~='button-main-node__groupSelect']:first-child {
  display: flex;
  align-items: center;
  justify-content: center;
}
ons-button[modifier~='button-main-node__groupSelect']:active {
  transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
  background-color: #26CA42;
}
